import React from "react";
import Card from "react-bootstrap/Card";
import "bootstrap/dist/css/bootstrap.min.css";

function KphComponent() {
  return (
    <div className="d-flex justify-content-around">
      <Card style={{ width: "18rem" }}>
        <Card.Body>
          <Card.Title>KPH</Card.Title>
          <Card.Text>
            By na bieco z prac naszego KPH kliknij tutaj <br /> Jako
            Organizacja Przyjaci Harcerstwa (OPH) jestemy czwarta gazi
            organizacyjna ZHP doczajc do Organizacji Harcerzy, Organizacji
            Harcerek oraz Starszego Harcerstwa. <br /> OPH okrelaj i
            obowizuj dwa regulaminy: Regulamin Gwny ZHP (dziaajcego poza
            granicami Kraju) <br /> Regulamin Kola Przyjaci Harcerstwa OPH
            zrzesza Kola Przyjaci Harcerstwa (KPH) dziaajce przy jednostkach
            harcerskich na danym terenie.
          </Card.Text>
        </Card.Body>
      </Card>

      <Card style={{ width: "18rem" }}>
        <Card.Body>
          <Card.Title>Cele KPH</Card.Title>
          <Card.Text>
            Celem kadego Kola oprcz propagowania idei harcerstwa jest cisa
            wsppraca z jednostk lub jednostkami harcerskimi, ktrymi si
            opiekuje...
          </Card.Text>
        </Card.Body>
      </Card>

      <Card style={{ width: "18rem" }}>
        <Card.Body>
          <Card.Title>Czonkowie KPH</Card.Title>
          <Card.Text>
            Czonkiem KPH moe by kady, kto ukoczy 18 lat i kto akceptuje
            idee harcerstwa zawarte w Przyrzeczeniu i Prawie Harcerskim...
          </Card.Text>
        </Card.Body>
      </Card>

      <Card style={{ width: "18rem" }}>
        <Card.Body>
          <Card.Title>Czonkowie OPH</Card.Title>
          <Card.Text>
            Czonkowie OPH dziel si na trzy kategorie: Czonkowie KPH,
            Czonkowie wspierajcy, Dziaacze/dziaaczki harcerskie OPH...
          </Card.Text>
        </Card.Body>
      </Card>
    </div>
  );
}

export default KphComponent;
