import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import "bootstrap/dist/css/bootstrap.min.css";

function HomeCardComponent() {
  return (
    <Row xs={1} md={1} className="g-4">
      {Array.from({ length: 1 }).map((_, idx) => (
        <Col key={idx}>
          <div className="p-3">
            <Card className="p-3">
              <Card.Body>
                <Card.Title>Co o nas!</Card.Title>
                <Card.Text>
                  semka powstaa w 1959 roku na terenach Pnocno-Zachodniego
                  Londynu, przy Parafii Matki Boej Miosierdzia na Willesden
                  Green. Wsppraca z Siostrzan Druyn Harcerek "Dunajec"
                  istnieje od pierwszych dni i stanowi wan cz naszej pracy
                  harcerskiej. semka przeprowadzia ponad 50 obozw, a gromada
                  zuchw jest dobrze prowadzona, co daje dobr nadziej na
                  przyszo semki.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Col>
      ))}
    </Row>
  );
}

export default HomeCardComponent;
